import * as THREE from "three";

import Common from "@/graphics/Common";
import Assets from "@/graphics/Assets";
import Device from "@/pure/Device";

import Atlas from "@/graphics/components/Atlas";
import MarquePage from "@/graphics/components/MarquePage";
import Ambiance from "@/graphics/components/Ambiance";
import Background from "@/graphics/components/Background";
import Circle from "@/graphics/components/Circle";
import Gallica from "@/graphics/components/Gallica/index.js";
import About from "@/graphics/components/About/index.js";
import CharacterMenu from "@/graphics/components/CharacterMenu/index.js";
// import Mountains from "@/graphics/components/Mountains";

import gsap from "@gsap/shockingly";
import normalizeWheel from "@/pure/NormalizeWheel";

export default class Output {
  components = {};
  postProcess = false;

  constructor() {
    this.init();
  }

  init() {
    this.components.atlas = new Atlas();
    this.components.circle = new Circle({
      diameter: 7,
      scale: 1,
      circle: 0.0075,
      opacity: 0.0
    });
    this.components.ambiance = new Ambiance();
    // this.components.mountains = new Mountains();

    this.page = new THREE.Group();
    this.page.matrixAutoUpdate = false;

    this.page.add(this.components.atlas);
    this.components.atlas.add(this.components.circle);
    this.page.add(this.components.ambiance);

    Assets.manager.onLoad = () => {
      this.components.atlas.init();
      this.components.atlas.enter();
      // this.components.mountains.init();
      this.components.marquePage = new MarquePage();
      this.page.add(this.components.marquePage);

      this.components.gallica = new Gallica();
      // console.log(this.components.gallica);
      this.components.atlas.add(this.components.gallica);

      this.components.about = new About();
      this.page.add(this.components.about);

      this.components.characterMenu = new CharacterMenu();
      this.page.add(this.components.characterMenu);

      this.components.gallica.rotation.y = 1.75;
      this.components.gallica.updateMatrix();
      this.components.gallica.enter();

      this.boundHandleWheel = this.handleWheel.bind(this);
      window.addEventListener('wheel', this.boundHandleWheel);

      if (Common.debug) {
        this.setDebug();
      }
    }
    Assets.manager.onStart = () => {
      const lerp = {
        x: 0
      }
      gsap.to(lerp, {
        x: 0.5,
        duration: 0.628,
        delay: 0.2,
        ease: "sine.inOut",
        onUpdate: () => {
          this.components.circle.material.uniforms.opacity.value = lerp.x
        }
      });
    }
    Assets.manager.onProgress = (url, loaded, total) => {
      const lerp = {
        x: this.components.circle.material.uniforms.arc.value.x,
        y: 0.0075,
        z: 0.5,
      }
      const x = loaded / total;
      if (this.tl) this.tl.kill();
      this.tl = gsap.timeline({ overwrite: true });
      this.tl.to(lerp, {
        x,
        duration: 1.5,
        delay: 0.2,
        ease: "sine.inOut",
        onUpdate: () => {
          this.components.circle.material.uniforms.arc.value.x = lerp.x
          this.components.circle.material.uniforms.arc.value.y = -lerp.x
        }
      }, 'a');
      if (x === 1) {
        this.tl.to(lerp, {
          y: 0,
          z: 0.1,
          duration: 0.5,
          ease: "sine.out",
          onUpdate: () => {
            this.components.circle.material.uniforms.opacity.value = lerp.z
            this.components.circle.material.uniforms.circle.value = lerp.y
          }
        }, 'b');
        this.components.ambiance.enter();
      }
    }
    Assets.init();
    
    Common.scene.add(this.page);
  }

  handleWheel(e) {
    const ne = normalizeWheel(e);
    const { pixelX, pixelY } = ne;
    const delta = Math.abs(pixelX) > Math.abs(pixelY) ? pixelX : pixelY;

    if(this.overwriteAnimation) this.overwriteAnimation.kill()
    // this.page.position.x -= delta / 100;
    // this.page.position.x = Math.min(this.page.position.x, 0);
    // this.page.updateMatrix();
    const offset = this.page.position.x - delta * 0.5;
    const x = Math.min(Math.max(offset, -(Device.fov.width + Device.fov.height * 0.75 + 9)), 0);
    this.overwriteAnimation = gsap.to(this.page.position, {
      overwrite: "auto",
      duration: 0.5,
      x,
      onUpdate: () => {
        this.page.updateMatrix();
      }
    });
  }
  
  animateSlider(direction) {
    this.animation.pause();
    if(this.overwriteAnimation) this.overwriteAnimation.kill()

    this.offsetX = gsap.utils.clamp(this.draggable.minX || -this.scrollWidth, this.draggable.maxX, gsap.getProperty(this.proxy, "x") - direction);

    this.overwriteAnimation = gsap.to(this.proxy, {
      overwrite: "auto",
      duration: this.slideDuration,
      x: this.offsetX,
      onUpdate: this.updateProgress
    });
  }

  addBackground({ el }) {
    this.components.background = new Background({
      el
    });
    this.page.add(this.components.background);
    this.components.background.resize();
  }

  updatePage(y) {
    // const p = y.end / Device.viewport.height;
    // const d = p *  Device.fov.height;
    // this.page.position.x = y.progress * d;
    this.page.position.x = y;
    this.page.updateMatrix();
  }

  render(t, delta) {
    Object.keys(this.components).forEach(_ => {
      this.components[_].render(t, delta);
    });

    Common.renderer.setRenderTarget(null);
    Common.render();
  }

  disposeComponent(_) {
    this.page.remove(this.components[_]);
    this.components[_].dispose();
    delete this.components[_];
  }

  dispose() {
    Object.keys(this.components).forEach(_ => {
      this.disposeComponent(_);
    });
    
    Common.scene.remove(this.page);
    this.page = null;
    
    window.removeEventListener('wheel', this.boundHandleWheel);
  }

  resize() {
    Object.keys(this.components).forEach(_ => {
      this.components[_].resize();
    });
  }

  setDebug() {
    Object.keys(this.components).forEach(_ => {
      if ('setDebug' in this.components[_]) {
        this.components[_].setDebug(Common.debug);
      }
    });
  }
}
