import Device from "@/pure/Device";
import {
    Group,
    PlaneGeometry,
    RawShaderMaterial,
    Mesh,
    Color,
    GLSL3,
    MeshBasicMaterial,
    AmbientLight,
    DirectionalLight,
    SkeletonHelper
} from "three";

// import { Glyph } from 'three-glyph';

import Assets from "@/graphics/Assets";

import Panel from "@/graphics/components/Panel/index.js";

import vertexShader from "./vertex.glsl";
import fragmentShader from "./fragment.glsl";
import Common from "@/graphics/Common";

export default class CharacterMenu extends Group {
    constructor() {
        super();
        this.setOrigin();
        this.resize();
    }
    setOrigin() {
        this.origin = new Group();

        this.geometry = new PlaneGeometry(2, 2, 1);
        this.material = new RawShaderMaterial({
          glslVersion: GLSL3,
          transparent: true,
          uniforms: {
            tDiffuse: {
                value: Assets.worldMap2,
            },
            tClouds: {
                value: Assets.worldMapClouds,
            },
            atmosphereColor: {
                value: new Color(0xaddfff),
            },
            time: {
                value: 0.
            }
          },
          vertexShader,
          fragmentShader,
        });
        this.mesh = new Mesh(this.geometry, this.material);
        this.origin.add(this.mesh);

        this.panel = new Panel({
            title: "EARTH — MILKY WAY",
            count: {
                label: "SURVEYED",
                type: "%",
                value: 0.0137,
            }
        });
        this.origin.add(this.panel);
        this.panel2 = new Panel({
            title: "ASTRODYNAMICS",
            count: {
                label: "CERTIFICATION",
                type: "%",
                value: 0.13,
            }
        });
        this.add(this.panel2);

        const geometry = new PlaneGeometry(2, 2, 1);
        const material = new MeshBasicMaterial({
            map: Assets.astodynamics,
            transparent: true
        });
        this.badge = new Mesh(geometry, material);
        this.panel2.add(this.badge);
        
        this.add(this.origin);

        this.ambientLight = new AmbientLight( 0x0000ff, 1 );
        this.add( this.ambientLight );

        this.directionalLight = new DirectionalLight( 0xff00ff, 5);
        this.add( this.directionalLight );

        Assets.dummy.scale.set(2.2, 2.2, 2.2);
        Assets.dummy.position.y = -1.7;
        this.add(Assets.dummy);


        this.skeleton = new SkeletonHelper( Assets.dummy );
        // this.skeleton.visible = false;
        Common.scene.add( this.skeleton );
    }
    render(t) {
        this.material.uniforms.time.value = t * 0.1;
    }
    resize() {
        const y = 1.5;
        this.position.x = Device.fov.width + Device.fov.height * 0.75 + 9;

        this.origin.position.x = -Device.fov.width * 0.5 + 1 + 0.5;
        this.origin.position.y = Device.fov.height * 0.5 - y - 0.5;

        this.panel2.position.x = Device.fov.width * 0.5 - 1 - 0.5;
        this.panel2.position.y = Device.fov.height * 0.5 - y - 0.5;
    }
    dispose() {
        this.remove(this.ambientLight);
        this.remove( this.directionalLight );
        this.remove(this.mesh);
        this.geometry.dispose();
        this.material.dispose();
        // this.glyph3.dispose();
        this.glyph4.dispose();
    }
}